.header-input {
  display: flex;
  width: 100%;
  max-width: 938px;
  box-sizing: border-box;
  min-height: 40px;
  margin: 20px auto 34px;
  border-radius: 4px;
  border: 1px solid #d9d9d9;
  padding: 9px 0 9px 12px;
}

.header-input::placeholder {
  color: #d9d9d9;
}
@media (max-width: 940px) {
  .header-input {
    width: 92.3%;
  }
}
